<template>
    <div class="vx-row">
        <div class="vx-col w-full lg:w-2/3 mb-base">

            <div class="vx-row">
                <div class="vx-col w-full lg:w-1/2  mb-base">

                    <vx-card  card-background="primary" style="height: 100%">

                        <h3 class="mb-5" style="color: white">Адрес в сети</h3>


                        <h2 class="mb-4" style="color: white">Host: <b>{{ $store.state.os.ip }}</b></h2>
                        <h2 style="color: white">Port: <b>3000</b></h2>


                    </vx-card>

                </div>

                <div class="vx-col w-full lg:w-1/2  mb-base">
                    <vx-card card-background="primary">
                        <h3 style="color: white"class="mb-5">Время работы</h3>
                        <h2 class="mb-4"  style="color: white">{{ upTime($store.state.os.startTime) }}</h2>

                        <vs-button class="w-full" color="white" text-color="#7367F0" icon-pack="feather" icon="icon-rotate-cw">Перезагрузить контроллер</vs-button>
                    </vx-card>
                </div>

                <div class="vx-col w-full lg:w-1/2 mb-base">
                    <vx-card>
                        <div>
                            <vue-apex-charts type=radialBar height=300 :options="chartOptionsCpu" :series="[$store.state.os.cpu]"/>
                        </div>
                    </vx-card>
                </div>

                <div class="vx-col w-full lg:w-1/2 mb-base">
                    <vx-card>
                        <div>
                            <vue-apex-charts type=radialBar height=300 :options="chartOptionsRam" :series="[$store.state.os.ram]"/>
                        </div>
                    </vx-card>
                </div>
            </div>
        </div>
        <div class="vx-col w-full lg:w-1/3 mb-base">
            <vx-card>
                <h3 class="mb-5">Версия ПО</h3>

                <h4 class="mb-4"><b>1.2.3</b></h4>
                <vs-button class="w-full" type="border" icon-pack="feather" icon="icon-download-cloud">Проверить обновления</vs-button>

                <vs-divider class="my-6"></vs-divider>

                <h3 class="mb-5">Оборудование</h3>
                <h4 class="mb-4">Всего секций: <b>22</b></h4>
                <h4 class="mb-4">Всего модулей: <b>150</b></h4>
                <vs-button class="w-full" type="border" icon-pack="feather" icon="icon-refresh-cw">Перезагузить модули</vs-button>

                <vs-divider class="my-6"></vs-divider>

                <h3 class="mb-5">Действия</h3>
                <vs-button class="w-full mb-2" type="border" icon-pack="feather" icon="icon-download">Импорт настроек</vs-button>
                <vs-button class="w-full mb-2" type="border" icon-pack="feather" icon="icon-upload">Экспорт настроек</vs-button>

            </vx-card>
        </div>
    </div>
</template>

<script>
    import VueApexCharts from 'vue-apexcharts'

    export default {
        components: {
            VueApexCharts
        },
        data(){
            return {
                chartOptionsCpu: {
                    labels: ['CPU', 'RAM'],
                    plotOptions: {
                        radialBar: {
                            size: 127,
                            offsetY: 0,
                            hollow: {
                                size: '50%'
                            },
                            track: {
                                background: "#ebebeb",
                                strokeWidth: '100%',
                                margin: 15,
                            },
                            dataLabels: {
                                show: true,
                                name: {
                                    fontSize: '18px',
                                },
                                value: {
                                    fontSize: '16px',
                                    color: "#636a71",
                                    offsetY: 11
                                },
                                total: {
                                    show: true,
                                    label: 'CPU',
                                    value: 56
                                }
                            }
                        },
                    },
                    responsive: [{
                        breakpoint: 576,
                        options: {
                            plotOptions: {
                                radialBar: {
                                    size: 150,
                                    hollow: {
                                        size: '20%'
                                    },
                                    track: {
                                        background: "#ebebeb",
                                        strokeWidth: '100%',
                                        margin: 15,
                                    },
                                }
                            }
                        }
                    }],
                    colors: ['#f91a1d'],
                    fill: {
                        type: 'gradient',
                        gradient: {
                            enabled: true,
                            shade: 'dark',
                            type: 'diagonal2',
                            shadeIntensity: 0.5,
                            gradientToColors: ['#7961F9'],
                            inverseColors: true,
                            opacityFrom: 1,
                            opacityTo: 1,
                            stops: [0, 100]
                        },
                    },
                    stroke: {
                        lineCap: 'round'
                    },
                    chart: {
                        dropShadow: {
                            enabled: true,
                            blur: 3,
                            left: 1,
                            top: 1,
                            opacity: 0.1
                        },
                    }
                },
                chartOptionsRam: {
                    labels: ['RAM'],
                    plotOptions: {
                        radialBar: {
                            size: 127,
                            offsetY: 0,
                            hollow: {
                                size: '50%'
                            },
                            track: {
                                background: "#ebebeb",
                                strokeWidth: '100%',
                                margin: 15,
                            },
                            dataLabels: {
                                show: true,
                                name: {
                                    fontSize: '18px',
                                },
                                value: {
                                    fontSize: '16px',
                                    color: "#636a71",
                                    offsetY: 11
                                },
                                total: {
                                    show: true,
                                    label: 'RAM',
                                    value: 56
                                }
                            }
                        },
                    },
                    responsive: [{
                        breakpoint: 576,
                        options: {
                            plotOptions: {
                                radialBar: {
                                    size: 150,
                                    hollow: {
                                        size: '20%'
                                    },
                                    track: {
                                        background: "#ebebeb",
                                        strokeWidth: '100%',
                                        margin: 15,
                                    },
                                }
                            }
                        }
                    }],
                    colors: ['#f91a1d'],
                    fill: {
                        type: 'gradient',
                        gradient: {
                            enabled: true,
                            shade: 'dark',
                            type: 'diagonal2',
                            shadeIntensity: 0.5,
                            gradientToColors: ['#7961F9'],
                            inverseColors: true,
                            opacityFrom: 1,
                            opacityTo: 1,
                            stops: [0, 100]
                        },
                    },
                    stroke: {
                        lineCap: 'round'
                    },
                    chart: {
                        dropShadow: {
                            enabled: true,
                            blur: 3,
                            left: 1,
                            top: 1,
                            opacity: 0.1
                        },
                    }
                }
            }
        },
        methods: {
            upTime(time){
                return this.$moment(time).toNow(true)
            }
        },
    }
</script>
